.site-header {
  .v-space();

  background-color: var(--c-primary);

  //box-shadow: inset unit((@h-space/2)/@base,rem) unit((@h-space/2)/@base,rem) 0 white, inset unit(-1 * (@h-space/2)/@base,rem) 0 0 white;;


  &, >* {
    position: relative;
  }

  svg path { fill: inherit; }

  &__background {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: unit(@v-space*2/@base,rem);
  }

  &__background-triangle {
    fill: white;
  }


  &__title {
    .gutter();
    .v-space();
    display: block;
    #type > #size > .h1;
    color: var(--c-primary-contrast);
    h1 {
      max-width: unit(@prose-width/@base,rem);
      margin-left: auto;
      margin-right: auto;
      //transform: ~"translateX( calc( -1 * (var(--gutter) / 4) ) )";
      font-weight: 900;
    }
  }

}
 .page-home .site-header__background-triangle {
   fill: #F5DD7C;
 }


 //move this:
 .site-background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  z-index: -1;
  display: none;
  &__triangle {
    fill: transparent;
  }
}

.page-home .site-background {
  display: block;
  &__triangle {
  fill: #F5DD7C;
  }
}
.page-work .site-header {
  .v-space(1,2);
}

.hero-background {
  position: absolute;
  bottom: 0;
  left: 0;
  height: unit(@v-space/@base,rem);
  fill: white;
}